function EventCard({ event, isAdmin, onAction }) {
  const [showRsvpOptions, setShowRsvpOptions] = useState(false);
  const [showDetails, setShowDetails] = useState(false);
  
  if (!event) return null;

  const isLiked = event.isLikedByUser || false;
  const isSaved = event.isSavedByUser || false;
  const likeCount = event.likeCount || 0;
  const saveCount = event.saveCount || 0;
  const participantCount = event.participantCount || 0;

  // Format date for display
  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric',
    });
  };

  // Format time for display
  const formatTime = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleTimeString('en-US', {
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  return (
    <Card className="overflow-hidden h-full flex flex-col relative">
      {/* Event Image */}
      <div className="h-40 w-full overflow-hidden bg-gray-200 relative">
        {event.imageUrl ? (
          <img 
            src={event.imageUrl} 
            alt={event.name} 
            className="w-full h-full object-cover"
          />
        ) : (
          <div className="flex items-center justify-center h-full bg-gradient-to-r from-blue-100 to-purple-100">
            <div className="text-2xl font-bold text-gray-500">{event.name.substring(0, 2).toUpperCase()}</div>
          </div>
        )}
        
        {/* Category Badge */}
        <Badge 
          className="absolute top-2 right-2 bg-opacity-80 backdrop-blur-sm"
          variant="secondary"
        >
          {event.category || "Event"}
        </Badge>
      </div>
      
      <CardHeader className="p-4 pb-2">
        <div className="flex justify-between items-start">
          <div>
            <div className="text-lg font-bold line-clamp-1">{event.name}</div>
            <div className="text-sm text-muted-foreground">
              {formatDate(event.startDate)}
            </div>
          </div>
        </div>
        
        {/* Move participant count here, below the title */}
        <div className="text-xs text-muted-foreground mt-1">
          {participantCount} {participantCount === 1 ? 'person' : 'people'} attending
        </div>
      </CardHeader>
      
      <CardContent className="p-4 pt-0 flex-grow">
        <p className="line-clamp-2 text-sm text-muted-foreground mb-2">
          {event.description}
        </p>
        <div className="text-xs text-muted-foreground flex items-center gap-1">
          <MapPin className="h-3 w-3" />
          <span className="line-clamp-1">{event.location}</span>
        </div>
        
        {/* Details Button */}
        <Button 
          variant="ghost" 
          size="sm" 
          className="mt-2 w-full text-xs"
          onClick={() => setShowDetails(true)}
        >
          View Details
        </Button>
      </CardContent>
      
      {/* Event Details Modal - only shows when details button is clicked */}
      {showDetails && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-background rounded-lg shadow-lg max-w-lg w-full max-h-[80vh] overflow-y-auto">
            <div className="p-6">
              <div className="flex justify-between items-start mb-4">
                <h3 className="font-bold text-xl">{event.name}</h3>
                <Button 
                  variant="ghost" 
                  size="sm"
                  className="h-8 w-8 p-0"
                  onClick={() => setShowDetails(false)}
                >
                  âœ•
                </Button>
              </div>
              
              <div className="mb-4">
                <div className="flex items-center gap-2 mb-2">
                  <Calendar className="h-4 w-4 text-muted-foreground" />
                  <span className="text-sm">
                    {formatDate(event.startDate)} at {formatTime(event.startDate)}
                  </span>
                </div>
                
                <div className="flex items-center gap-2 mb-4">
                  <MapPin className="h-4 w-4 text-muted-foreground" />
                  <span className="text-sm">{event.location}</span>
                </div>
                
                <div className="mb-4">
                  <Badge>{event.category || "Event"}</Badge>
                  {event.tags && event.tags.length > 0 && (
                    <div className="flex flex-wrap gap-1 mt-2">
                      {event.tags.map((tag, i) => (
                        <Badge key={i} variant="outline" className="text-xs">
                          {tag}
                        </Badge>
                      ))}
                    </div>
                  )}
                </div>
                
                <div className="mb-4">
                  <h4 className="font-medium mb-1">Description</h4>
                  <p className="text-sm text-muted-foreground">
                    {event.description}
                  </p>
                </div>
                
                <div className="text-sm mb-4">
                  <span className="font-medium">Attendees:</span> {participantCount}
                </div>
                
                <div className="flex gap-2 justify-end">
                  <Button
                    variant="outline"
                    onClick={() => setShowDetails(false)}
                  >
                    Close
                  </Button>
                  <Button
                    onClick={() => {
                      setShowRsvpOptions(true);
                      setShowDetails(false);
                    }}
                  >
                    RSVP
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
      
      <CardFooter className="p-4 pt-0 flex justify-between border-t">
        <div className="flex gap-2">
          <Button 
            variant="ghost" 
            size="sm" 
            className={`px-2 ${isLiked ? 'text-red-500' : ''}`} 
            onClick={() => onAction(event, 'like')}
            title={isLiked ? "Unlike" : "Like"}
          >
            <Heart className={`h-4 w-4 mr-1 ${isLiked ? 'fill-red-500' : ''}`} />
            <span>{likeCount}</span>
          </Button>
          
          <Button 
            variant="ghost" 
            size="sm" 
            className={`px-2 ${isSaved ? 'text-blue-500' : ''}`} 
            onClick={() => onAction(event, 'save')}
            title={isSaved ? "Unsave" : "Save"}
          >
            <BookmarkIcon className={`h-4 w-4 mr-1 ${isSaved ? 'fill-blue-500' : ''}`} />
            <span>{saveCount}</span>
          </Button>
        </div>

        {/* RSVP Button with Dropdown */}
        <div className="relative">
          <Button 
            variant="outline" 
            size="sm"
            className="px-4"
            onClick={() => setShowRsvpOptions(!showRsvpOptions)}
          >
            RSVP
          </Button>
          
          {showRsvpOptions && (
            <div className="absolute right-0 bottom-full mb-1 bg-background border rounded shadow-lg z-10">
              <Button 
                variant="ghost" 
                size="sm" 
                className="w-full justify-start" 
                onClick={() => {
                  onAction(event, 'rsvp', 'GOING');
                  setShowRsvpOptions(false);
                }}
              >
                Going
              </Button>
              <Button 
                variant="ghost" 
                size="sm" 
                className="w-full justify-start" 
                onClick={() => {
                  onAction(event, 'rsvp', 'MAYBE');
                  setShowRsvpOptions(false);
                }}
              >
                Maybe
              </Button>
              <Button 
                variant="ghost" 
                size="sm" 
                className="w-full justify-start" 
                onClick={() => {
                  onAction(event, 'rsvp', 'NOT_GOING');
                  setShowRsvpOptions(false);
                }}
              >
                Not Going
              </Button>
            </div>
          )}
        </div>
      </CardFooter>
    </Card>
  );
}